/* shutter navigation switcher */
a.shutter-nav-switcher {
  position: absolute;
  right: 5rem;
  top: 50%;
  margin: -31px -1.2rem 0;
  color: #000;
  text-decoration: none;
  width: 60px;
  height: 60px;
  will-change: color;
  transition-property: color;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.touchevents a.shutter-nav-switcher {
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

a.shutter-nav-switcher::before, a.shutter-nav-switcher::after, a.shutter-nav-switcher > i {
  display: block;
  position: absolute;
  left: 50%;
  height: 5px;
  background-color: currentColor;
  border-radius: 5px;
  will-change: transform, opacity;
  transition-property: transform, opacity;
  transition-duration: 0.2s;
  transition-timing-function: cubic-bezier(0.45, 0.05, 0.55, 0.95);
}

a.shutter-nav-switcher::before, a.shutter-nav-switcher::after {
  content: "";
  width: 40px;
  margin: 10px 0 11px -23px;
}

a.shutter-nav-switcher::before {
  bottom: 50%;
  transition-delay: 0s;
}

a.shutter-nav-switcher > i {
  font-size: 0;
  line-height: 0;
  top: 50%;
  width: 35px;
  margin: -3px 0 0 -18px;
  transition-delay: 0.05s;
}

a.shutter-nav-switcher::after {
  top: 50%;
  transition-delay: 0.1s;
}

a.shutter-nav-switcher > span {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

a.shutter-nav-switcher > span:active, a.shutter-nav-switcher > span:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

a.shutter-nav-switcher:hover {
  color: #ab52ff;
}

a.shutter-nav-switcher.shutter-nav_is-open::before, a.shutter-nav-switcher.shutter-nav_is-open::after, a.shutter-nav-switcher.shutter-nav_is-open > i {
  transform: translate(80px, 0) translateZ(0);
  opacity: 0;
}

/* shutter navigation switcher end */
/* shutter navigation close */
.shutter-nav-close {
  position: absolute;
  width: 3.1rem;
  height: 3.1rem;
  top: 3.9rem;
  right: 5.2rem;
  font-size: 0;
  line-height: 0;
  opacity: 0;
  transform: rotate(-20deg);
  transform: rotate(-20deg) translateZ(0);
  will-change: transform, opacity;
  transition-property: transform, opacity;
  transition-duration: 0s;
  transition-delay: 0.2s;
  transition-timing-function: ease-in-out;
}

.shutter-nav-close::before, .shutter-nav-close::after {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  height: 0.5rem;
  width: 4.1rem;
  margin: -0.3rem 0 0 -2.1rem;
  border-radius: 0.5rem;
  background-color: white;
  will-change: background-color;
  transition-property: background-color;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.shutter-nav-close::before {
  transform: rotate(45deg);
}

.shutter-nav-close::after {
  transform: rotate(-45deg);
}

.shutter-nav-close:hover::before, .shutter-nav-close:hover::after {
  background-color: #ab52ff;
}

.shutter-nav_is-open .shutter-nav-close {
  opacity: 1;
  transform: rotate(0deg);
  transform: rotate(0deg) translateZ(0);
  transition-duration: 0.4s;
  transition-delay: 0.75s;
}

