/* yield */
.yield {
  padding-top: 7rem;
  padding-bottom: 7rem;
}

.yield__holder {
  position: relative;
}

.yield__title {
  font-family: "proximanova-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 5.5rem;
  line-height: 1;
  color: #000;
  margin-bottom: 3rem;
}

.yield__title:last-child {
  margin-bottom: 0;
}

.yield-box {
  display: flex;
  height: 71.4rem;
}

.js .yield-box:not(.is-loaded) {
  opacity: 0;
  pointer-events: none;
  overflow: hidden;
}

.yield-box__aside {
  flex-basis: 45.2rem;
  flex-shrink: 0;
  height: 100%;
  margin-right: 6.1rem;
}

.yield-thumbs {
  position: relative;
  height: 100%;
  padding: 0 3rem;
  margin: 0 -3rem;
}

.yield-thumbs::before, .yield-thumbs::after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 22.3rem;
  pointer-events: none;
  left: 0;
  z-index: 5;
}

.yield-thumbs::before {
  top: 0;
  background: #eaefff;
  background: -webkit-linear-gradient(top, #eaefff 0%, rgba(234, 239, 255, 0) 100%);
  background: linear-gradient(to bottom, #eaefff 0%, rgba(234, 239, 255, 0) 100%);
}

.yield-thumbs::after {
  bottom: 0;
  background: #eaefff;
  background: -webkit-linear-gradient(bottom, #eaefff 0%, rgba(234, 239, 255, 0) 100%);
  background: linear-gradient(to top, #eaefff 0%, rgba(234, 239, 255, 0) 100%);
}

.yield-thumbs__item {
  opacity: 0.6;
  height: 22.3rem;
  will-change: opacity, background-color;
  transition-property: opacity, background-color;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.yield-thumbs__item.swiper-slide-active {
  opacity: 1;
}

.yield-thumbs__item.swiper-slide-thumb-active {
  background-color: #28e4cc;
  opacity: 1;
  cursor: default;
}

.yield-thumbs__item.swiper-slide-thumb-active .bond__rate {
  background-color: white;
  color: #28e4cc;
}

.yield-thumbs__item.swiper-slide-thumb-active .bond__title::after {
  display: none;
}

.yield-box__main {
  flex-grow: 1;
  flex-basis: calc(100% - 45.2rem - 6.1rem);
  max-width: calc(100% - 45.2rem - 6.1rem);
  height: 100%;
  padding: 3.9rem 0;
}

.yield-cards {
  height: 100%;
  position: relative;
}

.yield-cards .swiper-pagination {
  display: none;
}

.yield-cards__item {
  height: 100%;
}

/* yield end */
