@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

.input-wrap {
  position: relative;
  line-height: 1;
  @include inputs-spaces;

  @include media($form-media--mob) {
    @include inputs-spaces--mob; } }

label {
  &.input-wrap {
    display: block; } }

.input-def {
  display: block;
  height: $form-input-height;
  @include inputs-common-styles;
  @include inputs-font-styles;
  // transition
  $prop: color, border-color, background-color;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  // Для мобильных
  @include media($form-media--mob) {
    height: $form-input-height--mob;
    @include inputs-common-styles--mob;
    @include inputs-font-style--mob; } }

// Для textarea тоже использовать класс input-def
textarea.input-def {
  $lines: 5;
  $height: round($form-line-height * $lines) + $form-input-space-vt + $form-input-space-vb + round($form-border-width * 2);
  min-height: $height;
  height: $height;
  resize: vertical;

  // Для мобильных
  @include media($form-media--mob) {
    $height: round($form-line-height--mob * $lines) + $form-input-space-vt--mob + $form-input-space-vb--mob + round($form-border-width--mob * 2);
    min-height: $height;
    height: $height; }

  &[disabled] {
    resize: none; } }

// Placeholder
.input-def {
  @include placeholder {
    color: $form-placeholder-color;
    transition: $animation-speed opacity; }

  //form placeholder hide on focus
  @include placeholder-focus {
    opacity: 0; } }

// Стили для разных состояний полей
.input-def {
  &[disabled] {
    cursor: default;
    color: $form-text-color--disabled;
    background-color: $form-bg-color--disabled;
    border-color: $form-border-color--disabled;

    @include placeholder {
      color: $form-text-color--disabled; } }

  &:not([disabled]) {
    // Hover
    &:hover {
      border-color: $form-border-color_focused; }

    // Focus
    &:focus {
      @include form-on-focus; } }

  // Success
  &.success:not([disabled]) {
    @include form-success; }

  &.error:not([disabled]) {
    @include form-error; } }

@import "checkboxes";
@import "select2";
@import "validation";
