@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* === helper classes == */
.mark {
  color: $mark-color;
  font-style: inherit;
  font-weight: inherit; }

.text-color-alt {
  color: $base-color; }

.align-center {
  text-align: center; }

.align-left {
  text-align: left; }

.align-right {
  text-align: right; }

.float-left {
  float: left; }

.float-right {
  float: right; }

.nobr {
  white-space: nowrap; }

.hide-text {
  @include hide-text; }

.hide {
  display: none !important; }

.desktop-only {
  @include media('<tab-lg') {
    display: none; } }

.devices-only {
  @include media('>=tab-lg') {
    display: none; } }

.mobile-only {
  @include media('>=mob-lg') {
    display: none; } }

/* btn-app */
.btn-app {
  display: inline-block;
  vertical-align: top;
  background-color: black;
  background-repeat: no-repeat;
  height: 6.2rem;
  padding: 1rem 5rem 1rem 8rem;
  font-size: 1.8rem;
  line-height: 2.2rem;
  text-align: left;
  text-decoration: none;
  color: white;
  border-radius: 3.1rem;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  // transition
  $prop: background-color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  &:hover {
    background-color: $highlight-color--hover; } }

.btn-app_google {
  background-image: url(../../img/app-a.png);
  background-position: 2.8rem center;
  background-size: 29px auto; }

.btn-app_apple {
  background-image: url(../../img/app-ios.png);
  background-position: 28px center;
  background-size: 24px auto; }

/* btn-app end */

/* show elements on scroll */
html:not(.no-js) {
  .show-figure-js {
    visibility: hidden; } }

/* show elements on scroll end */

/* social share */
.soc-share-box {
  position: sticky;
  top: 10px;
  pointer-events: none;
  z-index: 5;

  margin-top: calc(-#{$heading-bottom} - 2.9rem);
  padding-bottom: $heading-bottom; }

.soc-share {
  pointer-events: auto; }

/* social share end */

/* chats */
.chats {
  overflow: hidden;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  cursor: default;

  @include media('<mob-lg') {
    justify-content: center; }

  &:last-child {
    margin-bottom: 0; } }

.chats__group {
  margin-left: 4rem;

  @include media('<desk-lg') {
    margin-left: 1.5rem; }

  @include media('<tab-lg') {
    margin-left: 1rem; }

  &:first-child {
    margin-left: 0; }

  & > strong {
    display: block;
    margin-bottom: 0.7rem;
    @include font-thin;
    font-size: 1.4rem;
    line-height: 1.8rem; } }

.chats__list {
  position: relative;
  display: inline-block;
  vertical-align: top;
  margin: 0 -15px -15px 0;

  @include media('<tab-lg') {
    margin: 0 -10px -10px 0; } }

.chats__item {
  float: left;
  margin: 0 15px 15px 0;

  a {
    display: block;
    position: relative;
    font-size: 60px;
    @include size(1em);
    color: inherit;

    @include media('<tab-lg') {
      font-size: 40px; }

    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      @include size(100%);
      background-image: url("../../img/soc-sprite.svg");
      background-repeat: no-repeat;
      background-size: 12em auto; } // размер файла спрайта, т.е. в спрайте 12 иконок размещенных по диагонали, 1em - 1 иконка

    &::after {
      opacity: 0; }

    // hover
    &:hover {
      &::before {
        opacity: 0; }

      &::after {
        opacity: 1; } }

    $soc-list: 'facebook', 'telegram', 'viber';
    $type-length: 4; // количество цветов одной иконки в спрайте, или через сколько единиц размещается иконка другогой социалки

    @for $i from 1 through length($soc-list) {
      $pos: $type-length - $type-length * $i * 1em; // на сколко единиц нужно сместить background

      //@debug $pos

      &.#{nth($soc-list, $i)} {
        &::before { // иконка с цветом в нормальном состоянии (фон без смещения - в спрайте такие иконки идут первыми: 1, 5, 9 и т.д.)
          $p: $pos;
          background-position: $p $p; }

        &::after { // икона с цветом при наведении (фон семстить на -1em - в спрайте такие иконки идут вторыми: 2, 6, 10 и т.д.)
          $p: $pos - 1;
          background-position: $p $p; }

        .user-form__msg__soc & {
          &::before { // иконка с другим цветом в нормальном состоянии (фон семстить на -2em - в спрайте такие иконки идут вторыми: 3, 7, 11 и т.д.)
            $p: $pos - 2;
            background-position: $p $p; }

          &::after { // иконка с другим цветом при наведении (фон семстить на -3em - в спрайте такие иконки идут вторыми: 4, 8, 12 и т.д.)
            $p: $pos - 3;
            background-position: $p $p; } } } }

    span { // Скрыть текст, но оставить видимым для озвучивающих программ
      @include hide-text; } } }

/* chats end */

/* === HEADER === */
.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  min-width: $layout-min-width;
  height: $header-height;
  z-index: $z-high;

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: white;
    opacity: 0;
    // transition
    $prop: opacity;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; }

    .page-scrolled & {
      opacity: 1; } } }

// Добавлять класс, если не нужно фиксить шапку
.header_float {
  position: relative;
  left: auto;
  top: auto; }

.header-frame {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%; }

/* logo */
.logo {
  float: left;
  display: block;
  position: relative;
  background-image: url(../../img/logo.png);
  background-repeat: no-repeat;
  background-size: contain;
  width: 187px;
  margin-bottom: 1.9rem;
  color: #cccddb;

  @media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi) {
    background-image: url(../../img/logo-2x.png); }

  @include media('<desk-lg') {
    background-image: url(../../img/logo-sm-2x.png);
    width: 165px;
    margin-top: -13px; }

  @include media('<tab-lg') {
    width: 93px;
    margin-top: 0;
    color: #a7a7ce; }

  &::before {
    content: "";
    display: block;
    @include ratio(187, 39); }

  a, & > span {
    display: block;
    overflow: hidden;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    text-indent: -9999px; } }

/* logo end */

/* navigation */
.nav-wrap {
  margin: 0.1rem 10rem 0 0; }

.nav {
  position: relative; }

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  @include font-bold;
  font-size: 1.8rem;
  line-height: 2.4rem;
  text-transform: uppercase;
  letter-spacing: 0.01em;
  cursor: default;
  @include clear;

  li {
    position: relative;
    float: left;
    margin-left: 5.7rem; }

  a {
    color: #000;
    text-decoration: none;
    transition: color $animation-speed;

    &:hover {
      color: $highlight-color--hover; } }

  // current
  li.current {
    color: $text-color-light;
    pointer-events: none;

    a, & > span {
      text-decoration: none;
      color: inherit;
      cursor: default; } } }

/* navigation end */

/* alternative navigation */
.alt-nav {
  position: relative; }

.alt-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  @include font-bold;
  font-size: 5.5rem;
  line-height: 6.6rem;
  letter-spacing: 0.01em;
  cursor: default;

  li {
    margin-bottom: 4.1rem;
    overflow: hidden; }

  a {
    color: #000;
    text-decoration: none;
    transition: color $animation-speed;
    padding-top: 0.5em;

    &:hover {
      color: $highlight-color--hover; } }

  // current
  li.current {
    color: $text-color-light;
    pointer-events: none;

    a, & > span {
      text-decoration: none;
      color: inherit;
      cursor: default; } } }

/* alternative navigation end */

/* === SHUTTER NAVIGATION === */
$switcher-nav-delay: 0.10s;
$switcher-nav-dur: 0.2s;
$toggle-nav-delay: $switcher-nav-delay + $switcher-nav-dur;
$toggle-nav-dur: 0.2s;
$toggle-nav-curtains-dur: 0.5s;
$toggle-nav-item-dur: 0.4s;
// =====================

/* shutter navigation switcher */
a.shutter-nav-switcher {
  $btn-nav-size: 60px;
  $btn-nav-lines-width: 35px;
  $btn-nav-lines-height: 5px;
  $btn-nav-lines-color: currentColor;
  // =====================

  position: absolute;
  right: nth($page-layout-space, 2);
  top: 50%;
  margin: -#{round-float($btn-nav-size / 2, 1) + 1} -1.2rem 0;
  color: #000;
  text-decoration: none;
  width: $btn-nav-size;
  height: $btn-nav-size;
  // transition
  $prop: color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $switcher-nav-dur;
    timing-function: ease-in-out; }

  // убрать подсветку при клике на тачскринах
  .touchevents & {
    user-select: none;
    -webkit-tap-highlight-color: transparent; }

  // верх, середина, низ
  &::before, &::after, & > i {
    display: block;
    position: absolute;
    left: 50%;
    height: $btn-nav-lines-height;
    background-color: $btn-nav-lines-color;
    border-radius: $btn-nav-lines-height;
    // transition
    $prop: transform, opacity;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $switcher-nav-dur;
      timing-function: cubic-bezier(0.45, 0.05, 0.55, 0.95); } }

  // верх, низ
  &::before, &::after {
    content: "";
    width: $btn-nav-lines-width + 5;
    margin: 10px 0 11px -#{round($btn-nav-lines-width / 2) + 5}; }

  // верх
  &::before {
    bottom: 50%;
    transition-delay: 0s; }

  // середина
  & > i {
    font-size: 0;
    line-height: 0;
    top: 50%;
    width: $btn-nav-lines-width;
    margin: -#{round($btn-nav-lines-height / 2)} 0 0 -#{round($btn-nav-lines-width / 2)};
    transition-delay: round-float($switcher-nav-delay / 2, 2); }

  // низ
  &::after {
    top: 50%;
    transition-delay: $switcher-nav-delay; }

  // текст
  & > span {
    @include hide-text; }

  // hover
  &:hover {
    color: $highlight-color--hover; }

  // opened
  &.shutter-nav_is-open {
    &::before, &::after, & > i {
      transform: translate(80px, 0) translateZ(0);
      opacity: 0; } } }

/* shutter navigation switcher end */

/* shutter navigation close */
.shutter-nav-close {
  position: absolute;
  width: 3.1rem;
  height: 3.1rem;
  top: 3.9rem;
  right: nth($page-layout-space, 2) + 0.2;
  font-size: 0;
  line-height: 0;
  opacity: 0;
  $rotate: -20deg;
  transform: rotate($rotate);
  transform: rotate($rotate) translateZ(0);
  // transition
  $prop: transform, opacity;
  will-change: $prop;
  transition: {
    property: $prop;
    // Кнопку закрытия скрывать с задержкой (сразу после закрытия меню), без анимацией
    duration: 0s;
    delay: $toggle-nav-dur;
    timing-function: ease-in-out; }

  &::before, &::after {
    $nav-close-cross-size: 0.5rem 4.1rem;
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    height: nth($nav-close-cross-size, 1);
    width: nth($nav-close-cross-size, 2);
    margin: -#{round-float(nth($nav-close-cross-size, 1) / 2, 1)} 0 0 -#{round-float(nth($nav-close-cross-size, 2) / 2, 1)};
    border-radius: nth($nav-close-cross-size, 1);
    background-color: white;
    // transition
    $prop: background-color;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; } }

  &::before {
    transform: rotate(45deg); }

  &::after {
    transform: rotate(-45deg); }

  &:hover {
    &::before, &::after {
      background-color: $highlight-color--hover; } }

  .shutter-nav_is-open & {
    opacity: 1;
    $rotate: 0deg;
    transform: rotate($rotate);
    transform: rotate($rotate) translateZ(0);
    transition: {
      // Кнопку закрытия показывать с задержкой и анимацией
      duration: $toggle-nav-item-dur;
      delay: $toggle-nav-delay + $toggle-nav-curtains-dur - 0.05s; } } }

/* shutter navigation close end */

/* shutter navigation */
.shutter-nav {
  position: fixed;
  right: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 9999;
  // Общий блок меню скрывать без задержки, с анимацией
  transition: {
    property: opacity, visibility;
    duration: $toggle-nav-dur; }

  &::before, &::after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    z-index: -1;
    // transition
    $prop: opacity, visibility, transform;
    will-change: $prop;
    transition: {
      property: $prop;
      // Шторки меню скрывать с задержкой (сразу после закрытия меню), без анимацией
      duration: 0s;
      delay: $toggle-nav-dur;
      timing-function: cubic-bezier(0.4, 0.6, 0.2, 1); } }

  &::before {
    background-color: white;
    $translate: 0, 100%;
    transform: translate($translate);
    transform: translate3d($translate, 0); }

  &::after {
    background-color: $base-color;
    left: 50%;
    $translate: 0, -100%;
    transform: translate($translate);
    transform: translate3d($translate, 0); }

  &.shutter-nav_is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    // Общий блок меню показывать без задержки и анимации
    transition: {
      duration: 0s;
      delay: 0s; }

    &::before, &::after {
      $translate: 0, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0);
      // Шторки меню показывать с задержкой и анимацией
      transition: {
        duration: $toggle-nav-curtains-dur;
        delay: $toggle-nav-delay; } } } }

.shutter-nav__layout {
  display: flex;
  align-items: flex-start;
  width: 100%; }

.shutter-nav__content,
.shutter-nav__footer {
  flex-grow: 1;
  flex-basis: 50%;
  max-width: 50%;
  padding: 3rem 9rem;
  opacity: 0;
  will-change: transform, opacity;
  transition: {
    property: transform, opacity;
    // Внутренние элементы скрывать с задержкой (сразу после закрытия меню), без анимацией
    duration: 0s;
    delay: $toggle-nav-dur;
    timing-function: ease-out; }

  .shutter-nav_is-open & {
    opacity: 1;
    $translate: 0, 0;
    transform: translate($translate);
    transform: translate3d($translate, 0);
    transition: {
      // Внутренние элементы показывать с задержкой и анимацией
      duration: $toggle-nav-item-dur;
      delay: $toggle-nav-delay + $toggle-nav-curtains-dur - 0.05s; } } }

// Левая часть
.shutter-nav__content {
  padding-left: 0;
  $translate: 0, 4rem;
  transform: translate($translate);
  transform: translate3d($translate, 0); }

// Правая часть
.shutter-nav__footer {
  padding-right: 0;
  color: white;
  $translate: 0, -4rem;
  transform: translate($translate);
  transform: translate3d($translate, 0); }

// title
.shutter-nav__title {
  @include font-extra-bold;
  font-size: 5.5rem;
  line-height: 6.6rem;
  letter-spacing: 0.01em;
  margin-bottom: 5.5rem;

  &:last-child {
    margin-bottom: 0; } }

// alternative navigation
.shutter-nav__alt-nav {
  margin: 0 0 15.5rem;

  &:last-child {
    margin-bottom: 0; } }

// app
.shutter-nav__app {
  margin-bottom: 2.5rem; }

.shutter-nav__app-item {
  margin-bottom: 3.0rem;

  &:last-child {
    margin-bottom: 0; } }

// conditions
.shutter-nav__conditions {
  overflow: hidden;
  width: 30rem;
  max-width: 100%;
  font-size: 1.8rem;
  line-height: 2.2rem;

  a {
    color: #000;
    padding-top: 0.5em;
    text-decoration: none;
    box-shadow: inset 0 -1px 0 #8f8f8f;
    transition: {
      property: color, box-shadow;
      duration: $animation-speed; }

    &:hover {
      box-shadow: inset 0 -1px 0 $highlight-color--hover;
      color: $highlight-color--hover; } } }

// contacts
.shutter-nav__contacts {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 13.5rem;
  padding: 0;
  font-size: 2.4rem;
  line-height: 3.2rem;
  list-style: none;

  &:last-child {
    margin-bottom: 0; }

  li {
    margin: 0 3rem 1.1rem 0; }

  a {
    $box-shadow: inset 0 -1px 0;
    color: inherit;
    text-decoration: none;
    box-shadow: #{$box-shadow} rgba(white, 0.5);
    transition: {
      property: color, box-shadow;
      duration: $animation-speed; }

    &[href^="tel:"] {
      box-shadow: none; }

    &:hover {
      color: $highlight-color--hover;
      box-shadow: #{$box-shadow} rgba($highlight-color--hover, 0.5);

      &[href^="tel:"] {
        box-shadow: none; } }

    & > strong {
      @include font-semi-bold;
      font-size: 3.0rem; } } }

// callback-form
.n-callback-form {
  margin-bottom: 7.1rem;

  &:last-child {
    margin-bottom: 0; }

  [class*="btn-"] {
    box-shadow: 0 10px 10px rgba(81, 0, 33, 0.25);

    &:hover {
      box-shadow: 0 5px 5px rgba($btn-bg-hover-color, 0.25); } } }

.n-callback-form__title {
  display: block;
  @include font-semi-bold;
  font-size: 2.4rem;
  line-height: 3.0rem;
  margin-bottom: 3.1rem; }

$n-callback-form-item-space: 2rem 2rem;
.n-callback-form__fields {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -#{round-float(nth($n-callback-form-item-space, 2) / 2, 1)};

  .input-wrap {
    flex-grow: 1;
    flex-basis: 50%;
    max-width: 50%;
    text-align: right;
    margin: 0;
    padding: 0 #{round-float(nth($n-callback-form-item-space, 2) / 2, 1)} nth($n-callback-form-item-space, 1); }

  .input-def {
    border-radius: 10px;
    background-color: white;
    border-right-color: white; } }

// chats
.shutter-nav__chats {
  .chats__group {
    & > strong {
      margin-bottom: 1.5rem; } } }

/* shutter navigation end */

/* === CONTENT === */
.main {
  flex-shrink: 1;
  flex-grow: 1;
  position: relative;
  //padding-top: 17.6rem
  padding-top: $header-height;
  clear: both;
  z-index: 4;

  .header_float ~ & {
    //padding-top: 6.5rem
    padding-top: 0; } }

/* === FOOTER === */
.footer {
  position: relative;
  font-size: 1.8rem;
  line-height: 2.2rem;
  color: white;
  background-color: #6228f2;
  z-index: 5;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    @include linear-gradient(to bottom, #5b2fc9 0%, #5352ce 100%);
    height: 19rem;
    z-index: -1; }

  *::selection {
    background-color: black; } }

.footer-holder {
  position: relative;
  min-height: 59.2rem;
  padding-top: 7.7rem;
  padding-bottom: 12.9rem;
  @include clear; }

/* footer contacts */
.footer__contacts {
  float: right;
  clear: right;
  width: $footer-right-col-width;
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 1.6rem;
  padding: 0 0 0 nth($footer-grid-space, 2);
  font-size: 2.4rem;
  line-height: 3.2rem;
  list-style: none;

  li {
    margin: 0 4.1rem 2rem 0;

    &.adr, &.cond {
      display: none; } }

  a {
    color: white;
    text-decoration: none;
    box-shadow: inset 0 -1px 0 rgba(white, 0.5);
    transition: {
      property: color, box-shadow;
      duration: $animation-speed; }

    &[href^="tel:"] {
      box-shadow: none; }

    & > strong {
      @include font-semi-bold;
      font-size: 3.0rem; }

    &:hover {
      color: $highlight-color--hover;
      box-shadow: inset 0 -1px 0 rgba($highlight-color--hover, 0.5);

      &[href^="tel:"] {
        box-shadow: none; } } } }

/* footer contacts end */

/* chats */
.footer__chats {
  position: absolute;
  left: 0.2rem;
  top: 42.4rem;
  color: white; }

/* chats end */

/* appeal form */
.footer__callback-form {
  float: right;
  clear: right;
  width: $footer-right-col-width;
