      color: inherit;
      cursor: pointer;
      background-color: rgba(white, 0.15);
      border: 1px solid transparent;
      border-radius: 1rem;
      -webkit-tap-highlight-color: transparent;
      $prop: background-color, border-color;
      transition: {
        property: $prop;
        duration: $animation-speed;
        timing-function: ease-in-out; }

      &:last-child {
        margin-bottom: 0; }

      // hover
