      min-width: 100%;
      font-size: 1.8rem;
      line-height: 2.2rem;
      margin-bottom: 2rem;
      color: white;
      transition: color $animation-speed;

      &:last-child {
        margin-bottom: 0; }

      // current
      #{$root-elem}.current & {
        color: $text-color-alt; } }

    .manual__step-figure {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      right: 0.2rem;
      top: 0;
      bottom: 0;
      width: 43.8rem;
      max-width: 50%;
      opacity: 0;
      pointer-events: none;
      // transition
      $prop: opacity;
      will-change: $prop;
      transition: {
        property: $prop;
        duration: $animation-speed; }

