  line-height: 1;
  color: #000;
  text-align: center;
  margin-bottom: 3rem;

  &:last-child {
    margin-bottom: 0; } }

.download__text {
  font-size: 1.8rem;
  line-height: 2.2rem;
  margin-bottom: 4.5rem;
  text-align: center;

  &:last-child {
    margin-bottom: 0; }

  p {
    margin-bottom: 2.2rem;

    &:last-child {
      margin-bottom: 0; } } }

.download__text-entry {
  color: $text-color-alt; }

.download__text-highlight {
  color: $highlight-color; }

$download-app-spaces: 4rem;
.download__app {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin: 0 -#{round-float($download-app-spaces / 2, 1)} 4rem;
  text-align: center;

  &:last-child {
    margin-bottom: 0; } }

.download__app-item {
  margin: 0 #{round-float($download-app-spaces / 2, 1)} 2rem; }

.download__note {
  font-size: 1.9rem;
  line-height: 2.4rem;
  color: rgba(#1e202b, 0.8); }

/* download end */
