@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

[class|="btn"], [class*=" btn-"], input[type="submit"], input[type="button"], input[type="reset"], button {
  display: inline-block;
  vertical-align: top;
  position: relative;
  text-align: center;
  text-decoration: none;
  cursor: pointer; }

[class|="btn"], [class*=" btn-"] {
  & input[type="submit"], & input[type="button"], & input[type="reset"] {
    background: url("../../img/empty.png") repeat;
    display: block;
    @include cover-abs();
    padding: 0;
    border: none;
    z-index: 99;
    opacity: 0; } }

/* btn-def */
.btn-def {
  position: relative;
  padding: 1.7rem 2.8rem 1.6rem;
  margin: 0;
  @include font-normal;
  color: $btn-text-color;
  //background: $btn-bg-color
  @include linear-gradient(to bottom, $btn-bg-color 0, $btn-bg-color 50%, $btn-bg-hover-color 50%, $btn-bg-hover-color 100%);
  background-size: 100% 200%;
  border: none;
  box-shadow: 0 10px 10px rgba($btn-bg-color, 0.25);
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  // transition
  $prop: background-position, color, box-shadow;
  transition: {
    property: $prop;
    duration: 0.6s;
    timing-function: cubic-bezier(0.4, 0, 0.2, 1); }

  @at-root {
    &,
    input[type="submit"]#{&},
    input[type="button"]#{&},
    input[type="reset"]#{&} {
      border-radius: 2.7rem; } } // Через @at-root, чтобы можно было перебить обнуленный border-radius в reset.sass

  &, & > span {
    @include font(1.8rem, 2.2rem);
    letter-spacing: 0.05em;
    text-transform: uppercase; }

  // hover
  &:not(:disabled):not(.disabled):not([data-disabled]):hover {
    //background-color: $btn-bg-hover-color
    background-position: 0 100%;
    box-shadow: 0 10px 10px rgba($btn-bg-hover-color, 0.25);
    transition-duration: 0.6s; }

  // active
  &:not(:disabled):not(.disabled):not([data-disabled]):active {
    top: 2px;
    box-shadow: 0 8px 8px rgba($btn-bg-hover-color, 0.25); }

  // disabled
  &:disabled, &.disabled, &[data-disabled] {
    cursor: default;
    pointer-events: none !important;
    background-color: $btn-disabled-color !important;
    box-shadow: 0 5px 5px rgba($btn-disabled-color, 0.25) !important; } }

/* btn-def end */

/* btn-alt */
.btn-alt {
  position: relative;
  padding: 1.7rem 2.8rem 1.6rem;
  margin: 0;
  @include font-normal;
  color: $btn-alt-text-color;
  background: $btn-alt-bg-color;
  @include linear-gradient(to bottom, $btn-alt-bg-color 0, $btn-alt-bg-color 50%, $btn-bg-hover-color 50%, $btn-bg-hover-color 100%);
  background-size: 100% 200%;
  border: none;
  box-shadow: 0 10px 10px 0 rgba(64, 69, 93, 0.25);
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  // transition
  $prop: background-position, color, box-shadow;
  transition: {
    property: $prop;
    duration: 0.6s;
    timing-function: cubic-bezier(0.4, 0, 0.2, 1); }

  @at-root {
    &,
    input[type="submit"]#{&},
    input[type="button"]#{&},
    input[type="reset"]#{&} {
      border-radius: 2.7rem; } } // Через @at-root, чтобы можно было перебить обнуленный border-radius в reset.sass

  &, & > span {
    @include font(1.8rem, 2.2rem);
    letter-spacing: 0.05em;
    text-transform: uppercase; }

  // hover
  &:hover {
    //background-color: $btn-bg-hover-color
    background-position: 0 100%;
    box-shadow: 0 10px 10px rgba($btn-bg-hover-color, 0.25); }

  // active
  &:active {
    top: 2px;
    transition: box-shadow 0s;
    box-shadow: 0 5px 5px rgba($btn-bg-hover-color, 0.25); }

  // disabled
  &:disabled, &.disabled, &[data-disabled] {
    cursor: default;
    pointer-events: none !important;
    background-color: $btn-disabled-color !important;
    box-shadow: 0 10px 10px rgba($btn-disabled-color, 0.25) !important;

    &::before, &::after {
      display: none; } } }

/* btn-alt end */
