@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* swiper slider */
/* slider pagination */
.swiper-pagination {
  pointer-events: none; }

.swiper-pagination-bullet {
  $size: 1.2rem 3.6rem;
  display: inline-block;
  vertical-align: top;
  height: nth($size, 1);
  width: 10rem;
  max-width: nth($size, 1);
  border-radius: round-float(nth($size, 1) / 2, 1);
  border: none;
  margin: 1rem 0.5rem 0;
  opacity: 1;
  pointer-events: auto;
  background: #40455d;
  outline: none;
  // transition
  $prop: opacity, max-width, background-color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  // hover
  &:not(.swiper-pagination-bullet-active):hover {
    background-color: $highlight-color--hover; }

  &.swiper-pagination-bullet-active {
    cursor: default;
    max-width: nth($size, 2);
    opacity: 0.2; } }

.swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 1rem 0.5rem 0; }


/* swiper slider end */
