/* reset */
*, *::after, *::before {
  box-sizing: border-box; }

body {
  -webkit-font-smoothing: antialiased; }

body, html {
  height: 100%;
  width: 100%; }

form {
  margin: 0;
  padding: 0; }
//a, input, button,
//a:focus, input:focus, button:focus
input, input:focus, textarea, select {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
  -webkit-focus-ring-color: rgba(255, 255, 255, 0) !important;
  outline: none !important; }
//*:focus
//utline: none
//moz-outline: 3px solid white !important
button,
textarea,
.select select,
input[type="text"],
input[type="email"],
input[type="search"],
input[type="submit"],
input[type="button"],
input[type="reset"],
input[type="number"],
input[type="range"] {
  //-webkit-appearance: none
  //-moz-appearance: none
  appearance: none;
 }  //border-radius: 0

select {
  &[disabled] {
    cursor: default; }

  &::-ms-expand {
    display: none; } }

input {
  vertical-align: middle;

  &::-ms-clear {
    display: none; }

  &[type="radio"]:focus {
    -moz-outline-radius: 12px !important;
    -moz-outline-offset: 0 !important; }

  &[type="checkbox"]:focus {
    -moz-outline-offset: -1px !important;
    -moz-outline: 1px solid #000 !important; } }

input[type="radio"],
input[type="checkbox"],
select {
  cursor: pointer; }

